import {actionTypes} from '@servicenow/ui-core';
import {ACTIONS, APIS, CASE_REPORT_TABLE} from './constants';
import {createHttpEffect} from '@servicenow/ui-effect-http';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import caseReportQuery from './caseReportQuery';
import { t } from 'sn-translate';

const onBootstrap = ({properties: { sysid }, dispatch}) => {
	dispatch(ACTIONS.CASE_REPORT_RECORD_DATA_FETCH_REQUESTED, {
		'qc': 'case='+sysid
	});
};

const onCaseReportDataFetchRequest = {
	...createGraphQLEffect(caseReportQuery, {
		variableList: ['qc'],
		successActionType: ACTIONS.CASE_REPORT_RECORD_DATA_FETCH_SUCCEEDED,
		errorActionType: ACTIONS.CASE_REPORT_RECORD_DATA_FETCH_FAILED
	}),
	stopPropagation: true
};

const onCaseReportDataFetch = ({
	action,
	updateState,
	properties: {
		solutionName,
		sysid
	},
	dispatch
}) => {
	action.stopPropagation();
	const {
		payload: {
			data: {
				GlideRecord_Query: {
					sn_customerservice_case_report: {
						_results: result
					}
				}
			}
		}
	} = action;
	if(result && result.length == 1){
		let caseReportSysId = result[0].sys_id.value;
		dispatch(ACTIONS.PREDICTION_DATA_FETCH_REQUESTED, {
			'solution_name': solutionName,
			'table': CASE_REPORT_TABLE,
			'sysid': caseReportSysId,
			'case_sys_id': sysid,
			'format_result':true
		});
	}
	else{
		updateState({
			isLoading: false,
			hasError: true
		});
	}
	return;
};

const onCaseReportDataFetchFail = ({action, updateState}) => {
	console.error('Case report data fetch failed');
	action.stopPropagation();
	updateState({
		isLoading: false,
		hasError: true
	});
};

const onPredictionFetchRequest = {
	...createHttpEffect(APIS.PREDICTION_API, {
		method: 'GET',
		pathParams: ['table','sysid'],
		queryParams: ['solution_name', 'case_sys_id', 'format_result'],
		successActionType: ACTIONS.PREDICTION_DATA_FETCH_SUCCEEDED,
		errorActionType: ACTIONS.PREDICTION_DATA_FETCH_FAILED
	}),
	stopPropagation: true
};

const onPredictionFetchFail = ({action, updateState}) => {
	console.error('Prediction fetch failed');
	action.stopPropagation();
	updateState({
		isLoading: false,
		hasError: true
	});
};

const onPredictionFetch = ({
	action,
	updateState
}) => {
	const {
		payload: {
			result: result
		}
	} = action;
	action.stopPropagation();
	updateState({
		isLoading: false,
		...result
	});
};

const actionHandlers = {
	[actionTypes.COMPONENT_BOOTSTRAPPED]: onBootstrap,
	[ACTIONS.CASE_REPORT_RECORD_DATA_FETCH_REQUESTED]: onCaseReportDataFetchRequest,
	[ACTIONS.CASE_REPORT_RECORD_DATA_FETCH_SUCCEEDED]: onCaseReportDataFetch,
	[ACTIONS.CASE_REPORT_RECORD_DATA_FETCH_FAILED]: onCaseReportDataFetchFail,
	[ACTIONS.PREDICTION_DATA_FETCH_REQUESTED]: onPredictionFetchRequest,
	[ACTIONS.PREDICTION_DATA_FETCH_SUCCEEDED]: onPredictionFetch,
	[ACTIONS.PREDICTION_DATA_FETCH_FAILED]: onPredictionFetchFail
};

export default actionHandlers;