import '@servicenow/now-loader';
import '@devsnc/sn-ettr-card';
import { t } from 'sn-translate';

const renderErrorState = (cardTitle, infoMessage, errorMessage)=>{
	if(!errorMessage){
		errorMessage = t('Contact your ServiceNow administrator for more information.')
	}
	//TODO: add error message as field in API response
	return <div className="root-element">
		<sn-ettr-card
			className="ettr-card"
			cardTitle={cardTitle}
			infoMessage={infoMessage}
			errorMessage={errorMessage}
		/>
	</div>;
};

const getEstimation = (lower_bound, upper_bound, unit) => {
	if(lower_bound == 0 && upper_bound == 0){
		//showing less than 1 day if both upper and lower bounds are 0
		return {
			estimationLabel: t('Estimated less than'),
			estimationValue: t('1 day')
		};
	}
	let estimationValue = '';
	let estimationLabel = '';
	if(lower_bound == 0){
		estimationLabel = t('Estimated less than');
		//had to do this for all translation keys to be captured
		switch(unit){
			case "minute":
				estimationValue = t("{0} minute", upper_bound);
				break;
			case "minutes":
				estimationValue = t("{0} minutes", upper_bound);
				break;
			case "hour":
				estimationValue = t("{0} hour", upper_bound);
				break;
			case "hours":
				estimationValue =  t("{0} hours", upper_bound);
				break;
			case "day":
				estimationValue = t("{0} day", upper_bound);
				break;
			case "days":
				estimationValue = t("{0} days", upper_bound);
				break;
			default:
				break;
		}
	}
	else{
		estimationLabel = t('Estimated up to');
		//had to do this for all translation keys to be captured
		switch(unit){
			case "minute":
				estimationValue = t("{0} to {1} minute", lower_bound, upper_bound);
				break;
			case "minutes":
				estimationValue = t("{0} to {1} minutes", lower_bound, upper_bound);
				break;
			case "hour":
				estimationValue = t("{0} to {1} hour", lower_bound, upper_bound);
				break;
			case "hours":
				estimationValue =  t("{0} to {1} hours", lower_bound, upper_bound);
				break;
			case "day":
				estimationValue = t("{0} to {1} day", lower_bound, upper_bound);
				break;
			case "days":
				estimationValue = t("{0} to {1} days", lower_bound, upper_bound);
				break;
			default:
				break;
		}
	}
	return {
		estimationLabel: estimationLabel,
		estimationValue: estimationValue
	};
};

const view = (state) => {
	const {properties: {
		cardTitle,
		infoMessage
	}, lower_bound, upper_bound, sla_message, unit, isLoading, hasError, errorMessage} = state;
	if(isLoading)
		return <div className="root-element is-loading">
			<sn-ettr-card
				className="ettr-card"
				cardTitle={cardTitle}
				infoMessage={infoMessage}
				isLoading={true}
			/>
		</div>;
	
	if(hasError || lower_bound == undefined || upper_bound == undefined || unit == undefined){
		return renderErrorState(cardTitle, infoMessage, errorMessage);
	}
	let {
		estimationLabel,
		estimationValue
	} = getEstimation(lower_bound, upper_bound, unit);
	return <div className="root-element">
		<sn-ettr-card
			className="ettr-card"
			cardTitle={cardTitle}
			infoMessage={infoMessage}
			slaMessage={sla_message}
			slaMessageColor="info"
			estimationLabel={estimationLabel}
			estimationValue={estimationValue}
			enableExplainability={false}
		>
		</sn-ettr-card>
	</div>;
};

export default view;
